<template>
  <div>
    <div class="search-box">
      <input
        type="text"
        class="search-bar"
        placeholder="Search..."
        v-model="query"
        @keypress="fetchWeather"
      />
    </div>

    <div class="intro-text" v-if="view === false">
      <!-- 검색 전 보여주는 화면 -->
      How is<br />
      the weather?!
    </div>

    <div class="weather-wrap" v-if="view === true">
      <!-- 검색 후 보여주는 화면 -->
      <div class="location-box">
        <div class="location">{{ name }}, {{ country }}</div>
        <div class="date">{{ dateBuilder() }}</div>
      </div>
      <div class="weather-box">
        <router-link to="/cocktail" class="temp"
          >{{ Math.round(temp) }}℃</router-link
        >
        <div class="weather">{{ main }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Weather",
  data() {
    return {
      view: false,
      url_base: "https://api.openweathermap.org/data/2.5/",
      api_key: "ecdbc8d28467ef44da9973c999eec9de",
      query: "",
      country: "",
      name: "",
      weather: "",
      temp: "",
      main: "",
    };
  },
  props: {
    cur: Boolean,
  },
  methods: {
    fetchWeather: function (e) {
      if (e.key == "Enter") {
        // enter키 감지
        let fetchUrl = `${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`;
        axios.get(fetchUrl).then((res) => {
          this.country = res.data.sys.country;
          this.name = res.data.name;
          this.weather = res.data.weather;
          this.temp = res.data.main.temp;
          this.main = res.data.weather[0].main;
          if (Math.round(this.temp) > 16) {
            // 기온이 16도 이상인지 판단
            this.warm = true;
            this.$emit("updateCur", true);
          } else {
            this.warm = false;
            this.$emit("updateCur", false);
          }
        });
        this.view = true; // api 데이터를 변수에 할당한 뒤에 돔을 그려주기 위함
      }
    },
    dateBuilder: function () {
      // 현재 기준 요일,날짜 받아 오는 함수
      let d = new Date();
      let months = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
      let days = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
      ];
      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();
      return `${day} ${date} ${month} ${year}`;
    },
  },
};
</script>

